@import url(https://fonts.googleapis.com/css?family=Archivo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html, body {
  width: 100%;
  height: 100%; }

html {
  overflow: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth; }

::-webkit-scrollbar {
  width: 0px;
  /* Remove scrollbar space */
  background: transparent;
  /* Optional: just make scrollbar invisible */ }

/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #FF0000; }

.row {
  display: flex;
  flex-direction: row; }

.col {
  display: flex;
  flex-direction: column; }

.around {
  justify-content: space-around; }

.between {
  justify-content: space-between; }

.center {
  justify-content: center; }

.fright {
  justify-content: flex-end; }

.intro-container {
  height: 100vh;
  background: -webkit-linear-gradient(315deg, #723362, #9d223c);
  background: linear-gradient(135deg, #723362, #9d223c);
  position: relative;
  z-index: 2; }

.fh {
  height: 100vh; }

.name-intro {
  color: #DDDDDD;
  font-size: 5vw;
  line-height: 20vh;
  font-family: "Archivo", sans-serif;
  letter-spacing: 2vw;
  text-align: center; }

.skills-intro {
  color: #AAAAAA;
  font-size: 4vw;
  font-family: "Archivo", sans-serif;
  text-align: center; }

.language {
  color: #DDDDDD;
  font-size: 3vw;
  line-height: 20vh;
  font-family: "Archivo", sans-serif;
  letter-spacing: 1.5vw;
  text-align: center;
  z-index: 1; }

.fadein {
  animation: fadein 10s;
  -moz-animation: fadein 10s;
  /* Firefox */
  -webkit-animation: fadein 10s;
  /* Safari and Chrome */
  -o-animation: fadein 10s;
  /* Opera */ }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fadein {
  /* Safari and Chrome */
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.down-arrow {
  animation: bounce 0.75s infinite alternate;
  -webkit-animation: bounce 0.75s infinite alternate;
  height: 6vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%; }

@keyframes bounce {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  to {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px); } }

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  to {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px); } }

.about-container {
  height: 100vh; }
  .about-container .background {
    height: 30vh;
    overflow-x: hidden;
    overflow-y: hidden;
    -webkit-transform: skewY(-12deg);
    transform: skewY(-12deg);
    -webkit-transform-origin: 0;
    transform-origin: 0;
    background: #3b8d99;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #3b8d99, #3b8d99, #3b8d99);
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#3b8d99), color-stop(#3b8d99), to(#3b8d99));
    background: -webkit-linear-gradient(left, #3b8d99, #3b8d99, #3b8d99);
    background: linear-gradient(to right, #3b8d99, #3b8d99, #3b8d99);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-position-x: 0;
    background-position-x: initial;
    background-position-y: 0;
    background-position-y: initial;
    background-size: auto auto;
    background-size: initial;
    background-repeat-x: initial;
    background-repeat-y: initial;
    background-attachment: scroll;
    background-attachment: initial;
    background-origin: padding-box;
    background-origin: initial;
    background-clip: border-box;
    background-clip: initial;
    background-color: transparent;
    background-color: initial;
    position: relative;
    z-index: 1; }
  .about-container .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #3b8d99;
    color: white;
    text-align: center; }

.footer-image {
  height: 2vh;
  width: 100%; }

.footer-image:hover {
  background-color: #66adad; }

.home-link-text {
  font-family: "Archivo", sans-serif;
  font-size: 1vh;
  text-decoration: none; }

.about-grid {
  display: grid;
  grid-template: 'skills header' 'skills header';
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr; }
  @media only screen and (max-width: 600px) {
    .about-grid {
      grid-template: 'skills skills' 'header header';
      grid-template-rows: 2fr 1fr; } }

.header {
  grid-area: header; }

.skills {
  grid-area: skills;
  width: 100%; }

.about-text {
  -webkit-transform: skewY(12deg);
  transform: skewY(12deg);
  -webkit-transform-origin: 0;
  margin: 10vh;
  font-size: 8vh;
  font-family: "Archivo", sans-serif;
  letter-spacing: 0.75vw;
  text-decoration: underline; }

.about-body {
  font-family: "Archivo", sans-serif;
  font-size: 2vh;
  text-align: left;
  padding: 1vh; }

.skills-text {
  color: #2F4F4F;
  font-size: 7vh; }

.skills-container {
  width: 90%;
  background-color: #ddd;
  margin: 1vw;
  font-family: "Archivo", sans-serif;
  font-size: 2vh;
  text-align: right;
  color: black;
  box-shadow: 0.6vh 0.5vh #3b8d99;
  height: 2vh;
  margin-bottom: 1vh; }

