$font: 'Archivo', sans-serif;
$skill-color: rgb(72, 146, 75);
$java: 80%;
$react: 70%;
$node: 85%;
$js: 85%;
$css: 60%;
$html5: 60%;
$python: 95%;
$aws: 65%;
$docker: 75%;
$kube: 65%;
$ts: 70%;
$sql: 75%;
$spring: 55%;
$c: 95%;
$cpp: 65%;
$go: 80%;

.about-container {
  height: 100vh;
  .background {
    height: 30vh;
    overflow-x: hidden;
    overflow-y: hidden;
    -webkit-transform: skewY(-12deg);
    transform: skewY(-12deg);
    -webkit-transform-origin: 0;
    transform-origin: 0;
    background: #3b8d99;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #3b8d99, #3b8d99, #3b8d99);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #3b8d99, #3b8d99, #3b8d99); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */;
    background-position-x: initial;
    background-position-y: initial;
    background-size: initial;
    background-repeat-x: initial;
    background-repeat-y: initial;
    background-attachment: initial;
    background-origin: initial;
    background-clip: initial;
    background-color: initial;
    position: relative;
    z-index: 1;
  }
  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #3b8d99;
    color: white;
    text-align: center;
  }
}


.footer-image {
  height: 2vh;
  width: 100%;
}

.footer-image:hover {
  background-color: rgb(102, 173, 173);
}
.home-link-text {
  font-family: $font;
  font-size: 1vh;
  text-decoration: none;
}

.about-grid {
  display: grid;
  grid-template: 
    'skills header'
    'skills header';
  @media only screen and (max-width: 600px) {
    grid-template: 
      'skills skills'
      'header header';
    grid-template-rows: 2fr 1fr;
  }
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr;
}

.header {
  grid-area: header;
}


.skills {
  grid-area: skills;
  width: 100%;
}

.about-text {
  -webkit-transform: skewY(12deg);
  transform: skewY(12deg);
  -webkit-transform-origin: 0;
  margin: 10vh;
  font-size: 8vh;
  font-family: $font;
  letter-spacing: 0.75vw;
  text-decoration: underline;
}

.about-body {
  font-family: $font;
  font-size: 2vh;
  text-align: left;
  padding: 1vh;
}

.skills-text {
  color: #2F4F4F;
  font-size: 7vh;
}

.skills-container {
  width: 90%;
  background-color: #ddd;
  margin: 1vw;
  font-family: $font;
  font-size: 2vh;
  text-align: right;
  color: black;
  box-shadow: 0.6vh 0.5vh #3b8d99;
  height: 2vh;
  margin-bottom: 1vh;

}

.java {width: $java; background-color: $skill-color; height: 2vh; animation: java 2s ease-out;}
.react {width: $react; background-color: $skill-color; height: 2vh; animation: react 2s ease-out;}
.node {width: $node; background-color: $skill-color; height: 2vh; animation: node 2s ease-out;}
.javascript {width: $js; background-color: $skill-color; height: 2vh; animation: javascript 2s ease-out;}
