.html5 {width: $html5; background-color: $skill-color; height: 2vh; animation: html5 2s ease-out;}
.python {width: $python; background-color: $skill-color; height: 2vh; animation: python 2s ease-out;}
.aws {width: $aws; background-color: $skill-color; height: 2vh; animation: aws 2s ease-out;}
.docker {width: $docker; background-color: $skill-color; height: 2vh; animation: docker 2s ease-out;}
.kubernetes {width: $kube; background-color: $skill-color; height: 2vh; animation: kubernetes 2s ease-out;}
.typescript {width: $ts; background-color: $skill-color; height: 2vh; animation: typescript 2s ease-out;}
.sql {width: $sql; background-color: $skill-color; height: 2vh; animation: sql 2s ease-out;}
.spring {width: $spring; background-color: $skill-color; height: 2vh; animation: spring 2s ease-out;}
.c {width: $c; background-color: $skill-color; height: 2vh; animation: c 2s ease-out;}
.cpp {width: $cpp; background-color: $skill-color; height: 2vh; animation: cpp 2s ease-out;}
.go {width: $go; background-color: $skill-color; height: 2vh; animation: go 2s ease-out;}




