$font: 'Archivo', sans-serif;

.intro-container {
  height: 100vh;
  background: linear-gradient(135deg, #723362, #9d223c);
  position: relative;
  z-index: 2;
}

.fh {
  height: 100vh;
}

.name-intro {
  color: #DDDDDD;
  font-size: 5vw;
  line-height: 20vh;
  font-family: $font;
  letter-spacing: 2vw;
  text-align: center;
}

.skills-intro {
  color: #AAAAAA;
  font-size: 4vw;
  font-family: $font;
  text-align: center;
 
}

.language {
  color: #DDDDDD;
  font-size: 3vw;
  line-height: 20vh;
  font-family: $font;
  letter-spacing: 1.5vw;
  text-align: center;
  z-index: 1;
}

.fadein {
  animation: fadein 10s;
  -moz-animation: fadein 10s; /* Firefox */
  -webkit-animation: fadein 10s; /* Safari and Chrome */
  -o-animation: fadein 10s; /* Opera */
}
@keyframes fadein {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}
@-moz-keyframes fadein { /* Firefox */
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}
@-o-keyframes fadein { /* Opera */
  from {
      opacity:0;
  }
  to {
      opacity: 1;
  }
}

.down-arrow {
  animation: bounce 0.75s infinite alternate;
  -webkit-animation: bounce 0.75s infinite alternate;
  height: 6vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

@keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-15px);
  }
}
@-webkit-keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-15px);
  }
}