.about-container {
  height: 100vh; }
  .about-container .background {
    height: 30vh;
    overflow-x: hidden;
    overflow-y: hidden;
    -webkit-transform: skewY(-12deg);
    transform: skewY(-12deg);
    -webkit-transform-origin: 0;
    transform-origin: 0;
    background: #3b8d99;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #3b8d99, #3b8d99, #3b8d99);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #3b8d99, #3b8d99, #3b8d99);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-position-x: initial;
    background-position-y: initial;
    background-size: initial;
    background-repeat-x: initial;
    background-repeat-y: initial;
    background-attachment: initial;
    background-origin: initial;
    background-clip: initial;
    background-color: initial;
    position: relative;
    z-index: 1; }
  .about-container .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #3b8d99;
    color: white;
    text-align: center; }

.footer-image {
  height: 2vh;
  width: 100%; }

.footer-image:hover {
  background-color: #66adad; }

.home-link-text {
  font-family: "Archivo", sans-serif;
  font-size: 1vh;
  text-decoration: none; }

.about-grid {
  display: grid;
  grid-template: 'skills header' 'skills header';
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr; }
  @media only screen and (max-width: 600px) {
    .about-grid {
      grid-template: 'skills skills' 'header header';
      grid-template-rows: 2fr 1fr; } }

.header {
  grid-area: header; }

.skills {
  grid-area: skills;
  width: 100%; }

.about-text {
  -webkit-transform: skewY(12deg);
  transform: skewY(12deg);
  -webkit-transform-origin: 0;
  margin: 10vh;
  font-size: 8vh;
  font-family: "Archivo", sans-serif;
  letter-spacing: 0.75vw;
  text-decoration: underline; }

.about-body {
  font-family: "Archivo", sans-serif;
  font-size: 2vh;
  text-align: left;
  padding: 1vh; }

.skills-text {
  color: #2F4F4F;
  font-size: 7vh; }

.skills-container {
  width: 90%;
  background-color: #ddd;
  margin: 1vw;
  font-family: "Archivo", sans-serif;
  font-size: 2vh;
  text-align: right;
  color: black;
  box-shadow: 0.6vh 0.5vh #3b8d99;
  height: 2vh;
  margin-bottom: 1vh; }

